@import './styles/variables';
@import './styles/extensions';
@import 'bootstrap';

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

@import './styles/flash-messages';

body {
  font-size: $font-size-sm;
  margin-top: 3.625rem;
  background-image: url('./assets/images/bg-desktop.png');
  background-position-x: 50%;
  background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed;

  &.rewards {
    background-image: url('./assets/images/loyalty-program/bg.svg');
  }
}

.container-min-height {
  min-height: calc(100vh - 73px - 223px);
}

.invalid-feedback {
  font-size: .75rem;
}

.nav-link {
  transition: none;

  .nav-icon {
    width: 1.5rem;
    height: 1.5rem;
    display: inline-block;

    &.nav-icon-trade {
      background-image: url('./assets/images/icons/Trade_normal_24.svg');
    }

    &.nav-icon-pools {
      background-image: url('./assets/images/icons/Pools_normal_24.svg');
    }

    &.nav-icon-faucet {
      background-image: url('./assets/images/icons/faucet_normal_24.svg');
    }

    &.nav-icon-doc {
      background-image: url('./assets/images/icons/gitbook_normal_24.svg');
    }
  }

  &:hover, &.active {
    .nav-icon-trade {
      background-image: url('./assets/images/icons/Trade_selected_24.svg');
    }

    .nav-icon-pools {
      background-image: url('./assets/images/icons/Pools_selected_24.svg');
    }

    .nav-icon-faucet {
      background-image: url('./assets/images/icons/faucet_selected_24.svg');
    }

    .nav-icon-doc {
      background-image: url('./assets/images/icons/gitbook_selected_24.svg');
    }
  }
}

.navbar-brand {
  img {
    height: 25px;
  }
}

ngx-money {
  font-family: var(--bs-font-monospace);
}

thead th {
  color: $gray-500;
}

.nav {
  &.nav-underscore {
    .nav-item {
      .nav-link {
        color: $navbar-dark-color;
        padding: .25rem 0;
        margin: 0;

        &.active {
          color: $white;
          border-bottom: 2px solid $primary;
          background: linear-gradient(180deg, rgba($primary, 0) 0%, rgba($primary, 0.15) 100%);
        }
      }

      & + .nav-item {
        margin-left: 1.5rem;
      }
    }
  }
}

.modal {
  .close {
    font-size: 24px;
    height: 24px;
    line-height: 24px;
  }
}

.modal-terms {
  max-width: 498px;

  .modal-title {
    text-align: center;
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    width: 100%;;
    background: var(--Gradient-green, linear-gradient(90deg, #07F1AB 0%, #A0FF88 100%));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.modal-campaign {
  max-width: 968px;

  .modal-content {
    overflow: hidden;
  }
}

.modal-referral {
  position: static;
  pointer-events: none;

  .modal-dialog {
    max-width: 573px;
    min-height: auto;
    margin-top: 0;
  }

  .modal-content {
    border-radius: 6px;
    border: 1px solid $primary-dark;
    backdrop-filter: none;
  }
}

.table-bordered-fusion {
  tr {
    td:first-child, th:first-child {
      border-left: 0;
    }
    td:last-child, th:last-child {
      border-right: 0;
    }

    &:last-child {
      border-bottom: 0;
    }
  }
}

.table-no-bottom-border {
  tr {
    &:last-child td {
      border-bottom: 0;
    }
  }
}

.table-nowrap {
  th, td {
    white-space: nowrap;
  }
}

.rotate-180 {
  transform: rotate(180deg);
}

.dropdown-menu-fadein {
  animation: fadeIn 0.3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}

.fs-6-5 {
  font-size: .875rem;
}

.fs-7 {
  font-size: .75rem !important;
}

.fs-5-5 {
  font-size: 1.125rem;
}

