.btn-primary {
  background: linear-gradient(90deg, $primary 0%, #0BD8E5 100%);

  &:hover {
    background: linear-gradient(90deg, #ACF7F1 0%, #ACF7F1 100%);
  }

  &:disabled {
    color: $primary-dark;
    background: #3E6473;
    opacity: 1;
    border-color: transparent;
  }
}

.btn-outline-primary-dark {
  background-color: $dark-theme-card-bg;

  &:hover, &.hover {
    border-color: $primary;
    background-color: $dark-theme-btn-hover;
  }
}

.bg-main-dark {
  background-color: $dark-theme-card-bg;
}

.bg-table-head {
  background-color: #162C3E !important;
}

.btn-icon {
  background-color: $dark-theme-card-bg;
  padding: 2px;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: $dark-theme-btn-hover;
  }
}

.btn-small {
  font-size: .75rem;
  font-weight: 400;
  line-height: 1rem;
  color: $primary;
  background-color: rgba(0, 22, 38, 0.75);
  padding: .125rem .5rem;
  border: 0;

  &:hover, &:focus {
    color: $primary;
    background-color: $dark-theme-btn-hover;
  }
}

.btn-expiry {
  background-color: rgba(48, 92, 133, 0.3);
  color: #A2D5DF;
  padding: .375rem 1rem;
  font-weight: 400;

  &.active, &:hover {
    background-color: rgba(0, 22, 38, 0.75);
    border-color: $primary;
  }
}

.modal-content {
  backdrop-filter: blur(9px);
}

.nav-combo {
  border-radius: 4px;
  border: 1px solid $primary-dark;
  background: $dark-theme-card-bg;
  padding: .25rem;

  .nav-link {
    color: $gray-500;
    line-height: 20px;

    &.active {
      color: $white;
      border-radius: 4px;
      background: #064C81;
    }
  }
}

.nav-combo-small {
  border-radius: 4px;
  background: $dark-theme-card-bg;
  padding: .125rem;
  display: inline-block;

  .nav-item {
    display: inline-flex;
  }

  .nav-link {
    color: $gray-500;
    line-height: 20px;
    padding: .125rem .75rem;

    &.active {
      color: $white;
      border-radius: 4px;
      background: #064C81;
    }
  }
}

.bg-transparent {
  background-color: transparent !important;
}

.dropdown-toggle {
  height: 42px;
}

.dropdown-toggle-no-icon {
  height: 24px;
  line-height: 24px;

  &::after {
    display: none;
  }
}

.dropdown-menu-dark {
  border: 1px solid $primary;
  box-shadow: 0px 4px 12px 0px rgba(134, 234, 224, 0.20);
}

.dropdown-item-icon {
  height: 20px;
  margin-right: 0.5rem;
}

.dropdown-popup {
  z-index: 2000 !important;
}

.flex-1 {
  flex: 1;
}

.form-check-input {
  margin-top: 0;
}

.tooltip-inner {
  backdrop-filter: blur(9px);
}

.btn:focus, .btn:active, .navbar-toggler:focus {
  box-shadow: none !important;
}

.text-table {
  color: #A2D5DF;
}

mark {
  background-color: transparent;

  a {
    color: #4DFFBF;
  }
}

.text-gradient-primary {
  background-image: linear-gradient(90deg, #07F1AB 0%, #A0FF88 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-gradient-danger {
  background-image: linear-gradient(90deg, #F44 0%, #FE7B7B 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-gradient-info {
  background-image: linear-gradient(90deg, #86EAE0 0%, #0BD8E5 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0px 4px 12px rgba(134, 234, 224, 0.20);
}

.modal-loading {
  z-index: 1057 !important;

  .modal-dialog {
    max-width: 280px;
    margin: auto;
  }

  .modal-content {
    border: 0;
    border-radius: 4px;
  }
}

.modal-loading-backdrop {
  z-index: 1056 !important;
}


.progress.progress-thin {
  flex: 1;
  height: .375rem;
  border-radius: .1875rem;
  background-color: $gray-900;

  .progress-bar {
    border-radius: 3px;
    background: linear-gradient(90deg, #07F1AB 0%, #A0FF88 100%);

    &.progress-warning {
      background: linear-gradient(90deg, #FFE500 0%, #FFE85F 100%);
      color: $warning;
    }

    &.progress-danger {
      background: linear-gradient(90deg, #F44 0%, #FE7B7B 100%);
      color: $danger;
    }

    &.progress-info {
      background: linear-gradient(90deg, #86EAE0 0%, #0BD8E5 100%);
      color: $info;
    }
  }
}


@media (max-width: 991px) {
  .navbar-collapse, .dropdown-popup > .dropdown-menu  {
    background-color: rgba(20, 42, 63, 0.80);
    position: fixed !important;
    left: 0 !important;
    bottom: 0 !important;
    backdrop-filter: blur(9px);
    border: 0;
    border-radius: 4px;
    border-top: 4px solid rgba(52, 169, 219, 0.50);
    width: 100%;
    z-index: 1060;
    max-height: 50vh;
    overflow: auto;
  }

  .navbar-toggler:after, .dropdown.dropdown-popup.show:after {
    content: '';
    display: block;
    position: fixed;
    z-index: 1059;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.30);
  }

  .navbar-toggler.collapsed:after {
    display: none;
  }

  .dropdown-popup > .dropdown-menu-fadein {
    top: initial !important;
    animation: slideIn 0.3s;
    animation-fill-mode: forwards;
  }

  .dropdown-popup {
    position: static !important;
    will-change: initial !important;
    transform: none !important;
    z-index: initial !important;
  }

  .modal-campaign {
    max-width: initial;

    img {
      width: 100%;
    }
  }

  .navbar-menu .dropdown-menu {
    box-shadow: none;
    border: 0;
    background-color: transparent;
    padding-left: 1rem;

    .dropdown-item {
      color: var(--bs-nav-link-color);
    }
  }
}

@media (max-width: 767px) {
  body {
    background-image: url('./assets/images/bg-mobile.png');

    &.rewards {
      background-image: url('./assets/images/loyalty-program/bg-mobile.svg');
    }
  }
}
