@import "~bootstrap/scss/bootstrap-utilities";

//Generate utilites for Cursor
$utilities: map-merge(
  $utilities,
  (
    "cursor": (
      property: cursor,
      values: auto default pointer wait text move help not-allowed
    ),
  )
);

@each $color, $value in $utilities-text-colors {
  .hover-text-#{$color} {
    &:hover {
      color: $value !important;
    }
  }
}
