.flash-messages {
  top: 58px !important;
  right: 16px !important;
  left: initial !important;
  width: 290px;
  overflow: hidden;

  .flash-message {
    background-color: rgba(20, 42, 63, 0.80) !important;
    border-radius: 4px;
    backdrop-filter: blur(9px);
    padding: 16px;
    padding-right: 50px;
    animation: slide 0.2s forwards;
    transform: translateX(100%);

    &.danger, &.error {
      color: #FF5656;
      border: 1px solid #FD2C52;
      box-shadow: 0px 0px 10px 0px rgba(253, 44, 82, 0.50);
    }

    &.alert, &.warning {
      color: #FF5656;
      border: 1px solid #FD2C52;
      box-shadow: 0px 0px 10px 0px rgba(253, 44, 82, 0.50);
    }

    &.success {
      color: #83FFBC;
      border: 1px solid #34F38C;
      box-shadow: 0px 0px 10px 0px rgba(52, 243, 140, 0.50);
    }

    &.info {
      color: #83FFBC;
      border: 1px solid #34F38C;
      box-shadow: 0px 0px 10px 0px rgba(52, 243, 140, 0.50);
    }
  }
}

.flash-message-close {
  float: initial;
  position: absolute;
  right: 16px;
  top: 16px;
  color: transparent;
  background-image: url('../assets/images/icons/close_24.svg');
}

@keyframes slide {
  100% {
    transform: translateX(0);
  }
}


@media (max-width: 767px) {
  .flash-messages {
    top: 58px !important;
    left: 16px !important;
    width: inherit;
  }
}
